import React from 'react';
import axios from 'axios';

const User = ({users}) => (
  <div>
    <ul>
      { console.log(users)}
    </ul>
  </div>
);

User.getInitialProps = async () => {
  const response = await axios.get(
    'https://swapi.co/api/planets/?page=1'
    );

  return { users: response.results};
}

export default User;
